
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name:'tj',
components: {
Flexbox,
FlexboxItem,
XButton,
Cell
},
data(){
return{
tit:''
}
},
methods:{


tj(){
const _this = this























if(this.grossMoney){   //总额度不为零,用户可以提交订单
this.$vux.confirm.show({
content: '确认要提交订单吗？',

onConfirm () {
_this.$router.push({path:'/orderFrom'});
_this.$store.commit('changeA')
}
})
}else{    //总额度为0
this.$vux.alert.show({
title: '注意',
content: '您暂未下单,无法提交订单',
})
}
},
},
computed:{

grossMoney(){
return this.$store.getters.grossMoney
},

isNullModal(){
return this.$store.getters.isNullModal
}
}
}



