export default {
    name:'single',
    data(){
        return{
          
        }
    },
    computed:{
        isChecked(){
            return this.$store.state.isChecked
        }
    },
    methods:{
        // 主动点击全选执行的事件
        allChecked(){
            if(this.$refs.all.checked == true){
                let msg = this.$refs.all.checked
                this.$store.commit('changeDisabled',msg )
            }else if(this.$refs.all.checked == false){
                let msg = this.$refs.all.checked
                this.$store.commit('changeFalse',msg)
            }
        }
    }
}



